.header-nav {
  display: flex;
  flex-direction: column;
}

@media (min-width: @tablet-width) {
  .header-nav {
    display: grid;
    grid-template-rows: 89px 70px;
    grid-template-areas:  "logo  user-menu"
      "site-menu-main  delivery";
  }
}

@media (min-width: @desktop-width) {
  .header-nav {
    display: grid;
    grid-template-columns: auto 149px auto;
    grid-template-rows: 89px 70px;
    grid-template-areas:  "site-menu-main  logo  user-menu"
      "site-menu-additional  divider  delivery";
  }
}

.header-nav__top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 79px;
  background-color: #ffffff;
  border-bottom: 1px solid #ececec;
}

@media (min-width: @tablet-width) {
  .header-nav__top-menu {
    display: block;
    grid-area: logo;
    height: 89px;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__top-menu {
    grid-area: logo;
  }
}

.header-nav__toggle-open {
  border: none;
  background-color: transparent;
  min-width: 20px;
  min-height: 14px;
  padding-top: 32px;
  padding-right: 31px;
  padding-bottom: 33px;
}

.header-nav__toggle-open--closed {
  display: block;
}

.header-nav__toggle-open--opened {
  display: none;
}

.header-nav__toggle-close {
  display: none;
}

.header-nav__toggle-close--opened {
  display: block;
  border: none;
  background-color: transparent;
  min-width: 21px;
  min-height: 20px;
  padding-top: 29px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
}

@media (min-width: @tablet-width) {
  .header-nav__toggle-open {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__toggle-open {
    display: none;
  }
}

.header-nav__logo {
  display: block;
  padding: 22px 67px 22px 116px;
  min-width: 86px;
  min-height: 35px;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .header-nav__logo {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__logo {
    display: none;
  }
}

.header-nav__logo-tablet {
  display: none;
}

@media (min-width: @tablet-width) {
  .header-nav__logo-tablet {
    display: block;
    padding-top: 21px;
    padding-bottom: 22px;
    padding-left: 50px;
    padding-right: 209px;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__logo-tablet {
    display: none;
  }
}

.header-nav__logo-desktop {
  display: none;
}

@media (min-width: @tablet-width) {
  .header__logo-desktop {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__logo-desktop {
    display: block;
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.header-nav__list-main--closed {
  display: none;
}

.header-nav__list-main--opened {
  position: absolute;
  display: block;
  top: 84px;
  right: 0;
  left: 0;
  bottom: 0;
}

@media (max-width: @mobile-width-only) {
  .header-nav__list-main--closed .header-nav__list-main--opened {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .header-nav__list-main {
    grid-area: site-menu-main;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__list-main {
    grid-area: site-menu-main;
    height: 89px;
    background-color: #ffffff;
    box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  }
}

.header-nav__list-additional {
  display: none;
}

@media (min-width: @tablet-width) {
  .header-nav__list-additional {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__list-additional {
    grid-area: site-menu-additional;
  }
}

.header-nav__user-menu--closed {
  display: none;
}

.header-nav__user-menu--opened {
  position: absolute;
  display: block;
  top: 244px;
  right: 0;
  left: 0;
  bottom: 0;
}

@media (max-width: @mobile-width-only) {
  .header-nav__user-menu--opened .header-nav__user-menu--closed {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .header-nav__user-menu {
    display: flex;
    justify-content: flex-end;
    grid-area: user-menu;
    height: 89px;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__user-menu {
    grid-area: user-menu;
  }
}

.header-nav__delivery {
  display: none;
}

@media (min-width: @tablet-width) {
  .header-nav__delivery {
    display: block;
    grid-area: delivery;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
    text-align: right;
    padding-top: 22px;
    padding-right: 50px;
  }
}

@media (min-width: @tablet-width) {
  .header-nav__delivery-additional-page {
    background-color: #f1f1f1;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__delivery {
    display: block;
    grid-area: delivery;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
    text-align: right;
    padding-top: 20px;
    padding-right: 51px;
  }
}

.header-nav__decoration {
  display: none;
}

@media (min-width: @tablet-width) {
  .header-nav__decoration {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .header-nav__decoration {
    display: block;
    grid-area: divider;
    background-color: #f1f1f1;
    width: 149px;
    height: 70px;
  }
}
