.featured-product {
  background-color: #ffffff;
  color: #000000;
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 46px;
}

@media (min-width: @tablet-width) {
  .featured-product {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (min-width: @tablet-width) {
  .featured-product__card {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-areas:
      "text image"
      "description image"
      "price image"
      "button image";
  }
}

@media (min-width: @desktop-width) {
  .featured-product__card {
    display: grid;
    grid-template-areas:
      "text image"
      "description image"
      "price image"
      "button image";
  }
}

.featured-product__title {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .featured-product__title {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
    color: #000000;
    padding-top: 38px;
    padding-bottom: 39px;
    padding-left: 50px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__title {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 53px;
    line-height: 57px;
    color: #000000;
    padding-top: 61px;
    padding-bottom: 35px;
    padding-left: 43px;
  }
}

.featured-product__weekly-product {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
}

@media (min-width: @tablet-width) {
  .featured-product__weekly-product {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 17px;
    text-transform: uppercase;
    text-align: center;
    width: 100px;
    height: 34px;
    padding-top: 46px;
    padding-bottom: 60px;
    padding-right: 50px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__weekly-product {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 17px;
    text-transform: uppercase;
    text-align: center;
    width: 100px;
    height: 34px;
    padding-top: 71px;
    padding-right: 43px;
    padding-bottom: 50px;
    padding-left: 82px;
  }
}

.featured-product__description-text {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0;
  padding-top: 18px;
  padding-bottom: 28px;
}

@media (min-width: @tablet-width) {
  .featured-product__description-text {
    grid-area: text;
    padding-top: 37px;
    padding-left: 50px;
    padding-bottom: 48px;
    padding-right: 42px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__description-text {
    position: absolute;
    top: 1050px;
    right: 575px;
    font-weight: 400;
    font-size: 17px;
    line-height: 30px;
    height: 90px;
    padding-top: 0;
    padding-right: 58px;
    padding-left: 225px;
    padding-bottom: 60px;
  }
}

.featured-product__description-list {
  list-style-type: none;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  margin: 0;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .featured-product__description-list {
    grid-area: description;
    padding-bottom: 57px;
    padding-left: 50px;
    padding-right: 42px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__description-list {
    height: 114px;
    padding-top: 0;
    padding-right: 58px;
    padding-bottom: 109px;
    padding-left: 225px;
  }
}

.featured-product__description-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #ececec;
}

.featured-product__description-term {
  text-align: left;
}

.featured-product__description-detail {
  margin: 0;
  text-align: right;
}

.featured-product__price {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
  margin: 0;
  padding-bottom: 25px;
}

@media (min-width: @tablet-width) {
  .featured-product__price {
    grid-area: price;
    padding-bottom: 37px;
    padding-left: 57px;
    padding-right: 103px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__price {
    padding-top: 0;
    padding-right: 117px;
    padding-bottom: 33px;
    padding-left: 225px;
  }
}

.featured-product__image-mobile {
  text-align: center;
  padding-top: 28px;
  padding-bottom: 25px;
}

@media (min-width: @tablet-width) {
  .featured-product__image-mobile {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__image-mobile {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .featured-product__image-tablet {
    display: block;
    grid-area: image;
    padding-top: 50px;
    padding-right: 50px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__image-tablet {
    display: none;
  }
}

@media (max-width: @mobile-width-only) {
  .featured-product__image-tablet {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .featured-product__image-desktop {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__image-desktop {
    display: block;
    grid-area: image;
    padding-right: 50px;
  }
}

@media (max-width: @mobile-width-only) {
  .featured-product__image-desktop {
    display: none;
  }
}

.featured-product__title-wrapper {
  display: flex;
  flex-direction: column;
}

@media (min-width: @tablet-width) {
  .featured-product__title-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__title-wrapper {
    position: absolute;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
  }
}

@media (min-width: @tablet-width) {
  .featured-product__button {
    grid-area: button;
    margin-left: 50px;
    margin-right: 42px;
  }
}

@media (min-width: @desktop-width) {
  .featured-product__button {
    margin-right: 58px;
    margin-left: 311px;
  }
}
